@charset "utf-8";
// 引用基础框架样式库
@import "lib/normalize";
* {
    -webkit-overflow-scrolling: touch;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    box-sizing: border-box;
}

body {
    background-color: #fff;
}

.q2 {
    .header {
        width: 100%;
        height: 494px;
        background-image: url(../images/top.png);
        background-size: contain;
    }
    .title {
        padding: 30px 0 10px 0;
        text-align: center;
        img {
            width: 158px;
            height: 33px;
        }
        p {
            font-size: 32px;
            color: #673f40;
            strong {
                color: #e64e5d;
            }
        }
    }
    .row {
        width: 100%;
        height: auto;
        background: #fff url(../images/bg.png) no-repeat center top;
        background-size: contain;
        ul {
            font-size: 0;
            padding: 35px 15px 0 0;
            text-align: center;
            li {
                width: 230px;
                display: inline-block;
                margin-left: 15px;
                *display: inline;
                *zoom: 1;
                &.special {
                    margin-left: 40px;
                }
            }
        }
        dt {
            font-size: 24px;
            padding: 20px 0 0 0;
            text-align: center;
            .small {
                font-size: 16px;
            }
            &.green {
                background-color: #ffedef;
                color: #e64e5d;
            }
            &.green2 {
                background-color: #f1fffb;
                color: #62d3b6;
            }
            &.green3 {
                background-color: #fff4e7;
                color: #e0b27b;
            }
        }
        .icon {
            height: 140px;
            background-color: #ffedef;
            display: block;
            vertical-align: middle;
            position: relative;
            &.icon1 {
                background-color: #f1fffb;
            }
            &.icon2 {
                background-color: #fff4e7;
            }
        }
        .answer {
            width: 800px;
            margin: auto;
            font-size: 16px;
            color: #7c5b5c;
            padding-left: 50px;
            *padding-left: 95px;
            margin-top: 30px;
            p {
                line-height: 27px;
                white-space: nowrap;
            }
            .tt {
                font-size: 18px;
            }
        }
    }
    .fix {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        margin: auto;
    }
    .play1 {
        width: 200px;
        height: 58px;
        display: block;
        background-image: url(../images/play1.png);
        margin: auto;
    }
    .play2 {
        width: 97px;
        height: 84px;
        display: block;
        background-image: url(../images/play2.png);
    }
    .play3 {
        width: 153px;
        height: 64px;
        display: block;
        background-image: url(../images/play3.png);
    }
    dd {
        background-color: #e64e5d;
        text-align: center;
        padding: 8px 0 6px 0;
        p {
            font-size: 26px;
            color: #fff;
            font-weight: bold;
        }
        a {
            font-size: 16px;
            color: #e64e5d;
            border-radius: 20px;
            display: inline-block;
            background-color: #fff;
            padding: 2px 30px;
            &.btn1 {
                color: #62d3b6;
                &.hvr-sweep-to-right::before {
                    background-color: #f1fffb;
                }
            }
            &.btn2 {
                color: #e0b27b;
                &.hvr-sweep-to-right::before {
                    background-color: #fff4e7;
                }
            }
        }
        &.item2 {
            background-color: #62d3b6;
        }
        &.item3 {
            background-color: #e0b27b;
        }
    }
}

.footer {
    text-align: center;
    margin: 50px 0;
    span {
        font-size: 18px;
        color: #7c5b5c;
        vertical-align: middle;
    }
    img {
        vertical-align: middle;
    }
}

.m {
    display: none;
}

@media screen and (min-width: 1024px) {
    .hvr-sweep-to-right {
        display: inline-block;
        vertical-align: middle;
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
        -moz-osx-font-smoothing: grayscale;
        position: relative;
        -webkit-transition-property: color;
        transition-property: color;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        border-radius: 20px;
    }
    .hvr-sweep-to-right::before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: #ffedef;
        border-radius: 20px;
        -webkit-transform: scaleX(0);
        transform: scaleX(0);
        -webkit-transform-origin: 0 50%;
        transform-origin: 0 50%;
        -webkit-transition-property: transform;
        -webkit-transition-property: -webkit-transform;
        transition-property: -webkit-transform;
        transition-property: transform;
        transition-property: transform, -webkit-transform;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-in-out;
        transition-timing-function: ease-in-out;
    }
    .hvr-sweep-to-right:hover,
    .hvr-sweep-to-right:focus,
    .hvr-sweep-to-right:active {
        color: #e64e5d;
        background: #fff\9;
        border-radius: 20px;
    }
    .hvr-sweep-to-right:hover::before,
    .hvr-sweep-to-right:focus::before,
    .hvr-sweep-to-right:active::before {
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
        border-radius: 20px;
    }
    .q2 .row ul li {
        i {
            transition: all ease-in-out .3s;
        }
    }
    .q2 .row ul li:hover {
        i {
            transform: scale(1.1);
            -webkit-transform: scale(1.1);
            -webkit-transform-origin: center;
            transform-origin: center;
        }
    }
}

@media screen and (max-width: 1200px) {
    .q2 .header {
        height: 410px;
    }
}

@media screen and (max-width:359px) and (min-width:320px) {
    html,
    body {
        font-size: 50px !important;
    }
}

@media screen and (max-width:374px) and (min-width:360px) {
    html,
    body {
        font-size: 56.25px !important;
    }
}

@media screen and (max-width:413px) and (min-width:375px) {
    html,
    body {
        font-size: 58.5px !important;
    }
}

@media screen and (max-width:639px) and (min-width:414px) {
    html,
    body {
        font-size: 64.6px !important;
    }
}

@media screen and (min-width:640px) {
    html,
    body {
        font-size: 12px !important;
    }
}

@media screen and (max-width: 600px) {
    .corp-hd,
    .corp-ft {
        display: none;
        opacity: 0;
    }
    .wrapper {
        width: 100%;
    }
    body {
        min-width: 320px;
    }
    .q2 .header {
        background-image: url(../images/m-top.png);
        height: 4.53rem;
        background-size: cover;
    }
    .q2 .title {
        padding: 0;
        margin-top: 0.24rem;
        img {
            width: 1.58rem;
            height: 0.33rem;
            display: block;
            margin: 0 auto 0.12rem;
        }
        p {
            font-size: 0.38rem;
            &.m {
                font-size: 0.24rem;
                display: block;
                color: #673f40;
            }
        }
    }
    .q2 .row ul {
        padding: 0;
        li,
        li.special {
            margin-left: 0;
            position: relative;
            width: 3rem;
            display: block;
            margin: 0.66rem auto 0;
            &::after {
                position: absolute;
                content: "";
                display: block;
                width: 1.11rem;
                height: 3.75rem;
                background-image: url(../images/right.png);
                background-size: cover;
                top: -0.3rem;
                right: -1.11rem;
            }
            &::before {
                position: absolute;
                content: "";
                display: block;
                width: 0.76rem;
                height: 2.85rem;
                background-image: url(../images/left.png);
                background-size: cover;
                bottom: 0;
                left: -0.76rem;
            }
        }
    }
    .q2 .row dt {
        font-size: 0.3rem;
        padding-top: 0.2rem;
        .small {
            font-size: 0.2rem;
        }
    }
    .q2 .row .icon {
        height: 1.8rem;
    }
    .q2 dd p {
        font-size: 0.32rem;
        margin-bottom: 0.05rem;
    }
    .q2 dd {
        padding: 0.12rem 0 0.15rem 0;
    }
    .q2 dd a {
        font-size: 0.2rem;
        border-radius: 0.13rem;
        padding: 0.03rem 0.4rem;
    }
    .q2 .play1 {
        width: 2rem;
        height: 0.58rem;
        background-size: cover;
    }
    .q2 .play2 {
        width: 0.97rem;
        height: 0.84rem;
        background-size: cover;
    }
    .q2 .play3 {
        width: 1.53rem;
        height: 0.64rem;
        background-size: cover;
    }
    .q2 .row .answer p {
        white-space: normal;
    }
    .q2 .row .answer {
        width: 100%;
    }
    .q2 .row {
        background-image: url(../images/m-bg.png);
        background-size: contain;
        margin: 0 0.2rem;
        width: calc(100% - 0.4rem);
    }
    .q2 .row .answer .tt {
        font-size: 0.22rem;
    }
    .q2 .row .answer {
        font-size: 0.2rem;
        padding-left: 0;
    }
    .q2 .row .answer p {
        line-height: 1.7;
    }
    .footer {
        margin: 0.2rem 0 0.5rem 0;
        span {
            font-size: 0.25rem;
        }
    }
    .footer img {
        width: 20px;
        &:nth-of-type(1) {
            width: 1.24rem;
            height: 0.28rem;
        }
        &:nth-of-type(2) {
            width: 1.27rem;
            height: 0.51rem;
            margin-left: -0.4rem;
        }
    }
}